import React, { useState, useRef } from "react";
import { useIntersection } from "../../hooks/IntersectionObserver";
import { design_process } from "../../utils/process";
import "./design_process.scss";

const DesignProcess = () => {
  const [isInView, setIsInView] = useState(false);
  const stepRef = useRef();

  useIntersection(stepRef, () => {
    setIsInView(true);
  });

  const setDelay = (delay) => {
    return { transitionDelay: `${delay}ms` };
  };

  return (
    <section id="process">
      <div className="container">
        <div className="process-intro">
          <h1>Design Process</h1>
          <p>
            these four steps are crucial for ensuring that the design process is
            focused, creative, and effective, and that the final design meets
            the needs of the client
          </p>
        </div>
        <div className="process-wrapper" ref={stepRef}>
          {design_process.map(
            ({ title, description, animDirection, animDelay }, index) => (
              <div
                className={`step ${animDirection} ${isInView ? "show" : ""}`}
                style={setDelay(animDelay)}
                key={index}
              >
                <div className="number" style={setDelay(animDelay)}>
                  <h1>{index + 1}</h1>
                </div>
                <div className="step-content">
                  <h2 style={setDelay(animDelay)}>{title}</h2>
                  <p style={setDelay(animDelay)}>{description}</p>
                </div>
              </div>
            )
          )}
        </div>
      </div>
    </section>
  );
};

export default DesignProcess;
